

/* Account  */

.account-Progressbar{
    height: 35px;
    width: 50% !important;
    border-radius: 5px;
}
.account-gb-text{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0px;
    font-weight: 500; 
}
.account-recharge-text{
    font-size: 15px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 800;
    font-size: 20px;
    margin-top: 23px;
}
.acoount-radio-box{
    margin-left: 15px;
}

.account-data-available{
    width: 50%;
    justify-content: space-between;
    display: flex;
    align-items: center;
    padding: 0 5px;
}

@media (min-width: 520px) and (max-width: 950px) {
    .account-Progressbar{  
     width: 80% !important;
     }

    .account-data-available{
        width: 80%;
    }
}
@media (min-width: 0px) and (max-width: 520px) {
    .account-Progressbar{  
        width: 100% !important;
    }
    
        .account-data-available{
            width: 100%;
        }
}

@media (min-width: 0px) and (max-width: 320px) {
    .account-recharge-text{
        font-size: 13px;
    }
}